import slugify from 'slugify';
import { shape, string } from 'prop-types';
import React, { useCallback } from 'react';
import { useSelector } from 'react-redux';
import { Link } from 'gatsby';

import { useQueryString } from 'hooks';
import { Layout, SEO, Filter } from 'components';

const Home = ({ location }) => {
  const querystring = useQueryString(location);
  const selector = useCallback(
    ({ jobs }) => {
      const { keywords } = querystring;
      const pattern = new RegExp(keywords, 'gi');

      return jobs.filter(
        ({ id, title, description, company }) =>
          !keywords ||
          !![
            !!id.match(pattern),
            !!title.match(pattern),
            !!description.match(pattern),
            !!company.match(pattern),
          ].filter(Boolean).length
      );
    },
    [querystring]
  );
  const jobs = useSelector(selector);
  const renderJob = useCallback(({ id, title }) => {
    const url = `/job/${id}/${slugify(title)}`.toLowerCase();

    return (
      <dl key={id}>
        <dt>
          <Link to={url}>{title}</Link>
        </dt>
      </dl>
    );
  }, []);

  return (
    <Layout>
      <SEO title="Home" />
      <h1>Latest jobs</h1>
      <Filter {...querystring} />
      {jobs.map(renderJob)}
    </Layout>
  );
};

Home.propTypes = {
  location: shape({ search: string.isRequired }).isRequired,
};

Home.defaultProps = {};

export default Home;
